body {
  margin: 0;
  padding-bottom: 5vh; /* Add padding bottom to account for footer height */
  overflow: hidden;
}

.footer {
  position: fixed; /* Set position to fixed */
  top: 95vh; /* Align footer to the bottom */
  left: 0;
  width: 100%;
  height: 5vh;
  text-align: center;
  padding: 0;
  background-color: #0b2447;
  color: #a5d7e8;
  z-index: 2;
  overflow: hidden;
}

@media screen and (max-width: 600px) and (max-height: 800px) {
  .footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 20vh;
    text-align: center;
    padding: 0;
    background-color: #0b2447;
    color: #a5d7e8;
    z-index: 2;
    font-size: 2vh;
    overflow: hidden;
  }
}
